.log-in, .log-out, .register {
  background-color: transparent;
  border-radius: 4px;
  /* min-height: 50px; */
  min-width: 100px;
  outline:0;
  cursor: pointer;
}


/* This line makes the "Register" and "Login" buttons look like links */
.link-button {
    background:none;
    color:inherit;
    border:none;
    padding:0;
    font: inherit;
    outline:0;
    border-bottom:1px solid #444; 
    cursor: pointer;
}
